import React from "react";
import {
  Layout,
  Row,
  Col,
  Button,
  Tooltip,
  Radio,
  Checkbox,
  Breadcrumb,
} from "antd";
import { NavLink } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import DuplicteDateTimeModal from "../modal/DuplicateDateTimeModal";
import styles from "./managePoll.module.scss";
import OrgSidebar from "../../containers/OrgSidebar";
import ReactStars from "react-rating-stars-component";
import { PaperClipOutlined } from "@ant-design/icons";
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  onChange: (newValue) => {
    console.log(`Example 3: new value is ${newValue}`);
  },
};

const { Content } = Layout;

const DuplicatePreviewPoll = ({
  poll,
  setPollsDate,
  backtoEdit,
  CreateDuplicatePoll,
  data,
  attachmentName,
}) => {
  const handlePollStartDatePickerChange = (date, name) => {
    setPollsDate(date, name);
  };

  const handlePollEndDatePickerChange = (date, name) => {
    setPollsDate(date, name);
  };

  const handlePollStartTimePickerChange = (time, name) => {
    setPollsDate(time, name);
  };

  const handlePollEndTimePickerChange = (time, name) => {
    setPollsDate(time, name);
  };

  const onBackCreatePoll = () => {
    backtoEdit(poll);
  };

  const onPublish = () => {
    CreateDuplicatePoll();
  };

  return (
    <>
      <React.Fragment>
        <React.Fragment>
          {" "}
          <Row className="inner-header">
            <LoggedinHeader amORGUser />
          </Row>
          <Row className="content-center pt-0">
            <Layout>
              <Layout>
                <OrgSidebar amORGUser data={data} />
                <Content>
                  <Row className="pt-3">
                    <Breadcrumb separator=">">
                      <NavLink to="/orgcc/pollsdirectory">
                        <Breadcrumb.Item>Manage Polls</Breadcrumb.Item>
                      </NavLink>
                      {/* <NavLink to="/orgcc/createpoll">
                        <Breadcrumb.Item>Create Poll</Breadcrumb.Item>
                      </NavLink>
                      <Breadcrumb.Item>Preview Poll</Breadcrumb.Item> */}
                    </Breadcrumb>
                  </Row>
                  <Row className="py-3 justify-content-between">
                    <h1 className="mb-0">Preview Poll</h1>

                    <Col
                      span={24}
                      lg={18}
                      className="d-flex align-items-center justify-content-end"
                    ></Col>
                  </Row>
                  <Col
                    span={24}
                    className={`mb-3 px-3 px-md-0 ${styles.poll_info}`}
                  >
                    <h6>
                      {poll.pollName}
                      <Tooltip placement="top" title={poll.pollHelpText}>
                        <span className="pl-2 icon icon-info-circle"></span>
                      </Tooltip>
                    </h6>
                  </Col>

                  <div className="mid-content">
                    <Row>
                      <Col span={24}>
                        <p className="font-weight-md6">{poll.pollQuestion}</p>
                        <Row className="preview-que" disabled={true}>
                          <div className="d-flex flex-column flex-gap chk-opt">
                            {poll.pollQuestionType === "1" ? (
                              poll.pollResponse.map((value) => (
                                <Radio>{value}</Radio>
                              ))
                            ) : poll.pollQuestionType === "2" ? (
                              poll.pollResponse.map((value) => (
                                <Checkbox>{value}</Checkbox>
                              ))
                            ) : poll.pollQuestionType === "3" ? (
                              poll.pollRatingType === "1" ? (
                                <ReactStars
                                {...starsRating}
                                value={poll.pollRatingResponse}
                                name="pollRatingResponse"
                                onChange={(value) =>
                                  handlePollOnSelect(
                                    value,
                                    "pollRatingResponse"
                                  )
                                }
                                classNames="mt-3"
                              />
                              ):
                              poll.pollRatingType === "2" ? (<img src={Smiley} alt="send message" />):
                              poll.pollRatingType === "3" ? (<img src={Thermometer} alt="send message" />):
                              poll.pollRatingType === "4" ? (<img src={Thumb} alt="send message" />):
                              poll.pollRatingType === "5" ? (<img src={Steps} alt="send message" />):
                              (null)
                            ) : null}
                          </div>
                        </Row>
                        <Row className="mt-2">
                          <div class="ant-upload-list-item">
                            <div class="ant-upload-list-item-info">
                              <span class="ant-upload-span">
                                <div class="ant-upload-text-icon">
                                  <PaperClipOutlined />
                                </div>
                                <span
                                  class="ant-upload-list-item-name"
                                  title="add-user-doc"
                                >
                                  {poll.pollDocument ? (
                                    <a
                                      href={poll.pollDocument}
                                      download={poll.pollDocument}
                                    >
                                      {attachmentName}
                                    </a>
                                  ) : null}
                                </span>
                                <span class="ant-upload-list-item-card-actions">
                                  <button
                                    type="button"
                                    class="ant-btn"
                                  ></button>
                                </span>
                              </span>
                            </div>
                          </div>
                        </Row>
                      </Col>
                    </Row>
                  </div>

                  <div className="mt-5 px-3">
                    <Button
                      type="secondary"
                      type="secondary"
                      htmlType="submit"
                      className="mr-4"
                      onClick={onBackCreatePoll}
                    >
                      Back
                    </Button>
                    <DuplicteDateTimeModal
                      pollData={poll}
                      handlePollStartDatePickerChange={
                        handlePollStartDatePickerChange
                      }
                      handlePollEndDatePickerChange={
                        handlePollEndDatePickerChange
                      }
                      handlePollStartTimePickerChange={
                        handlePollStartTimePickerChange
                      }
                      handlePollEndTimePickerChange={
                        handlePollEndTimePickerChange
                      }
                      onPublish={onPublish}
                      viaEdit
                    />
                  </div>
                </Content>
              </Layout>
            </Layout>
          </Row>
        </React.Fragment>
      </React.Fragment>
    </>
  );
};

export default DuplicatePreviewPoll;
