import React, { useEffect, useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Button,
  Select,
  Input,
  Switch,
  Alert,
  message,
  notification,
  Upload,
  Spin,
} from "antd";
import { useHistory, useLocation } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import styles from "./managePoll.module.scss";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import ReactStars from "react-rating-stars-component";
import MultiChoice from "./MultipleChoice";
import DuplicatePreviewPoll from "./DuplicatePreviewPoll";
import { CheckCircleTwoTone } from "@ant-design/icons";
import momentUtil from "../../utils/momentUtil";
import authHeader from "../../services/auth-header";
import axios from "axios";
import moment from "moment";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import UploadImage from "./UploadImage"
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  onChange: (newValue) => {
    console.log(`Example 3: new value is ${newValue}`);
  },
};

const { TextArea } = Input;
const { Content } = Layout;
var CryptoJS = require("crypto-js");

const DuplicatePoll = (props) => {
  const history = useHistory();
  const [attachmentName, setAttachmentName] = useState("");
  const [poll, setPoll] = useState({
    pollName: "",
    pollHelpText: "",
    pollDocument: "",
    showResult: false,
    pollChartType: "",
    pollQuestion: "",
    pollQuestionType: "",
    pollRatingResponse: 0,
    pollStartDate: "",
    pollEndDate: "",
    pollStartTime: "",
    pollEndTime: "",
    pollResponse: [],
    pollOptionText: [],
    pollId: "",
    status_id: "",
    pollRatingType:""
  });
  const [loader, setLoader] = useState(false);
  const [pollError, setpollError] = useState("");
  const [error, showError] = useState(false);
  const [chartError, showChartError] = useState(false);
  const [previewPoll, setPreviewPoll] = useState(false);
  const [uploadError, showuploadError] = useState(true);
  const [isImage, setIsImage] = useState(false);
  const [form] = Form.useForm();
  let location = useLocation();
  const [api, contextHolder] = notification.useNotification();

  const pollID = props.computedMatch?.params.id;

  useEffect(() => {
    if(pollID == 1  || pollID == 2 ){
      const options = {
        headers: {
          'Accept': "application/json",
          "Content-Type": "application/json",
        },
      };
      axios.get("http://localhost:3000/data/"+ `poll_${pollID}.json`, options).then((res) => {
        const data = res.data;
        setDefaultPollDetails(data[0]);
      })

    }else{
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios.get(BASE_URL + `getPoll/${pollID}/`, options).then((res) => {
      const data = res.data;
      setPollDetails(data);
    });
  }
  }, []);

  const setDefaultPollDetails = (data) =>{

    setPoll({
      ...poll,
      pollId: data.poll_id,
      pollName: data.title,
      pollHelpText: data.help_text,
      showResult: data.show_result_to_users,
      pollChartType: data.poll_result_chart_type.toString(),
      pollQuestion: data.question,
      pollQuestionType: data.poll_question_type.toString(),
      pollRatingResponse: "",
      pollStartDate: moment().utc().local().format("DD-MM-YYYY"),
      pollEndDate: moment().utc().local().format("DD-MM-YYYY"),
      pollStartTime: moment().utc().local().format("hh:mm:ss"),
      pollEndTime: moment().utc().local().format("hh:mm:ss"),
      status_id: data.status_id,
      pollResponse: data.option_text.map((value) => value.option_text),
      pollOptionText: data.option_text,
    });

    setAttachmentName(
      data.image_name !== null
        ? data.image_name
        : data.document_name !== null
        ? data.document_name
        : ""
    );
    const isImage =
      data.image
        ?.slice(data.image.lastIndexOf("."), data.image.length)
        .includes("jpg") ||
      data.image
        ?.slice(data.image.lastIndexOf("."), data.image.length)
        .includes("jpeg") ||
      data.image
        ?.slice(data.image.lastIndexOf("."), data.image.length)
        .includes("png");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
    form.setFieldsValue({
      pollName: data.title,
      pollHelpText: data.help_text,
      pollQuestion: data.question,
    });
    isPollNameUnique(data.title);


  }

  const setPollDetails = (data) =>{
    setPoll({
      ...poll,
      pollId: data.poll_id,
      pollName: data.title,
      pollHelpText: data.help_text,
      showResult: data.show_result_to_users,
      pollChartType: data.poll_result_chart_type.toString(),
      pollQuestion: data.question,
      pollQuestionType: data.poll_question_type.toString(),
      pollRatingType:data.poll_question_rating_type?data.poll_question_rating_type.toString():"",
      pollRatingResponse: "",
      pollStartDate: momentUtil.formatAsDDMMYYYY(data.start_date),
      pollEndDate: momentUtil.formatAsDDMMYYYY(data.end_date),
      pollStartTime: data.start_time,
      pollEndTime: data.end_time,
      status_id: data.status_id,
      pollResponse: data.option_text.map((value) => value.option_text),
      pollOptionText: data.option_text,
    });

    setAttachmentName(
      data.image_name !== null
        ? data.image_name
        : data.document_name !== null
        ? data.document_name
        : ""
    );
    const isImage =
      data.image
        ?.slice(data.image.lastIndexOf("."), data.image.length)
        .includes("jpg") ||
      data.image
        ?.slice(data.image.lastIndexOf("."), data.image.length)
        .includes("jpeg") ||
      data.image
        ?.slice(data.image.lastIndexOf("."), data.image.length)
        .includes("png");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
    form.setFieldsValue({
      pollName: data.title,
      pollHelpText: data.help_text,
      pollQuestion: data.question,
    });
    isPollNameUnique(data.title);

  }

  const setPollsDate = (date, name) => {
    setPoll({ ...poll, [name]: date });
  };

  const backtoEdit = (value) => {
    setPreviewPoll(false);
    setPoll({
      ...poll,
      pollName: value.pollName,
      pollHelpText: value.pollHelpText,
      pollDocument: value.pollDocument,
      showResult: value.showResult,
      pollChartType: value.pollChartType,
      pollQuestion: value.pollQuestion,
      pollQuestionType: value.pollQuestionType,
      pollRatingResponse: value.pollRatingResponse,
      pollStartDate: value.pollStartDate,
      pollEndDate: value.pollEndDate,
      pollResponse: value.pollResponse,
    });

    form.setFieldsValue({
      pollHelpText: value.pollHelpText,
      pollQuestion: value.pollQuestion,
    });
  };

  const onPreview = (e) => {
    e.preventDefault();
    if (
      uploadError &&
      poll.pollName &&
      poll.pollName.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollHelpText &&
      poll.pollHelpText.replace(/^\s*/, "").replace(/\s*$/, "").length > 0 &&
      poll.pollQuestionType &&
      poll.pollQuestionType.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      poll.pollQuestion
    ) {
      if (poll.showResult || poll.pollQuestionType === "3") {
        if (!poll.pollChartType && poll.showResult) {
          showError(true);
          setPreviewPoll(false);
        } else if(poll.pollQuestionType === "3" && !poll.pollRatingType) {
          showError(true);
          setPreviewPoll(false);
        }else{
          showError(false);
          setPreviewPoll(true);
        }
      } else {
        showError(false);
        setPreviewPoll(true);
      }
    } else {
      showError(true);
      setPreviewPoll(false);
    }
  };

  const CreateDuplicatePoll = () => {
    setLoader(true);
    const user = JSON.parse(AppStorage.get("user"));
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    let optionsData = [];
    poll.pollResponse.map((value) => optionsData.push({ option_text: value }));

    const finalOptions = optionsData.map((value) => {
      var option = Object.assign({}, value);
      poll.pollOptionText.map((item) => {
        if (item.option_text === value.option_text) {
          option.poll_option_id = item.poll_option_id;
          option.poll = poll.pollId;
        } else if (
          item.option_text !== value.option_text &&
          !option.poll_option_id
        ) {
          option.poll_option_id = "";
          option.poll = poll.pollId;
        }
      });
      return option;
    });

    let data = {};
    function convertImgToBase64(url, callback, outputFormat) {
      var canvas = document.createElement("CANVAS");
      var ctx = canvas.getContext("2d");
      var img = new Image();
      img.crossOrigin = "Anonymous";
      img.onload = function () {
        canvas.height = img.height;
        canvas.width = img.width;
        ctx.drawImage(img, 0, 0);
        var dataURL = canvas.toDataURL(outputFormat || "image/png");
        callback.call(this, dataURL);
        // Clean up
        canvas = null;
      };
      img.src = url;
    }
    const startDate = moment.isMoment(poll.pollStartDate)
      ? momentUtil.formatAsDDMMYYYY(poll.pollStartDate)
      : poll.pollStartDate;
    const endDate = moment.isMoment(poll.pollEndDate)
      ? momentUtil.formatAsDDMMYYYY(poll.pollEndDate)
      : poll.pollStartDate;
    const startTime =
      poll.pollStartTime instanceof Date
        ? momentUtil.formatAsHHmmss(poll.pollStartTime)
        : poll.pollStartTime;
    const endTime =
      poll.pollEndTime instanceof Date
        ? momentUtil.formatAsHHmmss(poll.pollEndTime)
        : poll.pollEndTime;
    if (poll.pollDocument?.length === 0) {
      data = {
        create_poll: {
          title: poll.pollName,
          help_text: poll.pollHelpText,
          show_result_to_users: poll.showResult ? 1 : 0,
          poll_result_chart_type: poll.pollChartType
            ? parseInt(poll.pollChartType)
            : 2,
          poll_question_type: parseInt(poll.pollQuestionType),
          poll_question_rating_type:poll.pollRatingType?parseInt(poll.pollRatingType):"",
          question: poll.pollQuestion,
          start_date: startDate,
          start_time: startTime,
          end_date: endDate,
          end_time: endTime,
          status_id: poll.status_id,
          poll_id: poll.pollId,
          created_by: parseInt(
            CryptoJS.AES.decrypt(
              user.user_details.org_user_id,
              "anonymoose"
            ).toString(CryptoJS.enc.Utf8)
          ),
        },
        option: finalOptions,
      };
      axios
        .post(BASE_URL + "poll/", data, options)
        .then(() => {
          setLoader(false);
          const placement = "bottomLeft";
          api.info({
            description: "Poll has been created successfully.",
            placement,
          });
          window.location = "/orgcc/pollsdirectory";
        });
    } else {
        if (isImage) {
          data = {
            create_poll: {
              title: poll.pollName,
              help_text: poll.pollHelpText,
              image: poll.pollDocument[0].base64,
              image_name: attachmentName,
              show_result_to_users: poll.showResult ? 1 : 0,
              poll_result_chart_type: poll.pollChartType
                ? parseInt(poll.pollChartType)
                : 2,
              poll_question_type: parseInt(poll.pollQuestionType),
              poll_question_rating_type:poll.pollRatingType?parseInt(poll.pollRatingType):"",
              question: poll.pollQuestion,
              start_date: startDate,
              start_time: startTime,
              end_date: endDate,
              end_time: endTime,
              created_by: parseInt(
                CryptoJS.AES.decrypt(
                  user.user_details.org_user_id,
                  "anonymoose"
                ).toString(CryptoJS.enc.Utf8)
              ),
            },
            option: finalOptions,
          };
        } else {
          data = {
            create_poll: {
              title: poll.pollName,
              help_text: poll.pollHelpText,
              document:  poll.pollDocument[0].base64,
              document_name: attachmentName,
              show_result_to_users: poll.showResult ? 1 : 0,
              poll_result_chart_type: poll.pollChartType
                ? parseInt(poll.pollChartType)
                : 2,
              poll_question_type: parseInt(poll.pollQuestionType),
              poll_question_rating_type:poll.pollRatingType?parseInt(poll.pollRatingType):"",
              question: poll.pollQuestion,
              start_date: startDate,
              start_time: startTime,
              end_date: endDate,
              end_time: endTime,
              created_by: parseInt(
                CryptoJS.AES.decrypt(
                  user.user_details.org_user_id,
                  "anonymoose"
                ).toString(CryptoJS.enc.Utf8)
              ),
            },
            option: finalOptions,
          };
        }
        axios
          .post(BASE_URL + "poll/", data, options)
          .then(() => {
            setLoader(false);
            const placement = "bottomLeft";
            api.info({
              description: "Poll has been created successfully.",
              placement,
            });
            window.location = "/orgcc/pollsdirectory";
          });
    }
  };

  const onChange = (checked, e) => {
    setPoll({ ...poll, [e.target.id]: checked });
  };

  const onCancel = () => {
    history.push("/orgcc/pollsdirectory");
  };

  const onCreateChange = (e) => {
    setPoll({ ...poll, [e.target.id]: e.target.value });
  };

  const checkChartType = (value) => {
    if (showResult && value === "3" && poll.pollChartType != "4") {
      showChartError(true);
    }
  };

  const handlePollOnSelect = (value, name) => {
    if (value === "3" && name === "pollQuestionType") {
      checkChartType(value);
    } else {
      showChartError(false);
    }
    setPoll({ ...poll, [name]: value });
  };

  const setSingleChoiceResponse = (value) => {
    let name = "pollResponse";
    setPoll({ ...poll, [name]: value });
  };

  const handleStepOneUpload = (value, name) => {
    
      if (value.file.status === "removed") {
        showuploadError(true);
      }
      let fileList = value.fileList.slice(-1);
      checkDoucmentType(value.file.type);
      fileList.forEach(function (file, index) {
        let reader = new FileReader();
        reader.onload = (e) => {
          file.base64 = e.target.result;
        };
        reader.readAsDataURL(file.originFileObj);
      });
      setAttachmentName(value.file.name);
      setPoll({ ...poll, [name]: fileList });
    
  };

  const checkDoucmentType = (fileType) => {
    const isImage = fileType?.includes("image");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
  };

  const handleOnRemove = () => {
    if (poll.status_id === 4) {
      return null;
    }
  };

  const handlePollImagePreview = async file => {
    file.preview = await getBase64(file.originFileObj);
    setPoll({...poll, previewImage : file.url || file.preview, previewVisible: true, 
      previewTitle: file.name || file.url.substring(file.url.lastIndexOf('/') + 1),

    })
  };

  const handleCancel = (value) =>  setPoll({...poll, previewVisible: false });

  const getBase64=(file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  }

  const handleBeforeUpload = (file, name) => {
    if (poll.status_id === 4) {
      return null;
    } else {
      let xlsxfileExt = file.name.includes(".xls");
      let pptxfileExt = file.name.includes(".pptx");
      let docfileExt = file.name.includes(".doc");
      let csvfileExt = file.name.includes(".csv");

      const isValidDoc =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif" ||
        file.type === "application/pdf" ||
        xlsxfileExt ||
        pptxfileExt ||
        docfileExt ||
        csvfileExt;

      if (!isValidDoc) {
        showuploadError(true);
        message.error(`${file.name} is not a supported format file type.`);
      } else {
        showuploadError(false);
      }
      const isImage =
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif";
      const isDocument =
        file.type === "application/pdf" ||
        xlsxfileExt ||
        pptxfileExt ||
        docfileExt ||
        csvfileExt;
      const isLt5M = file.size / 1024 / 1024 < 5;
      const isLt10M = file.size / 1024 / 1024 < 10;
      if (isImage) {
        if (!isLt5M && isImage) {
          showuploadError(false);
          message.error("Image must smaller than 5MB!");
        } else {
          showuploadError(true);
        }
      }

      if (isDocument) {
        if (!isLt10M && isDocument) {
          showuploadError(false);
          message.error("Document must smaller than 10MB!");
        } else {
          showuploadError(true);
        }
      }
      return (isValidDoc && isLt5M) || isLt10M;
    }
  };

  const isPollNameDuplicate = (e) => {
   if (e.target.value !== location.state.title) {
      isPollNameUnique(e.target.value)
    }
  };

  const isPollNameUnique=(value)=>{
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

      axios
        .post(BASE_URL + "checkpoll/", { title:value }, options)
        .then((res) => {
          if (res.data) {
            setpollError(res.data[0]);
          }
        });

  }

  return (
    <>
      {contextHolder}
      <React.Fragment>
        {!previewPoll ? (
          <React.Fragment>
            {" "}
            <Row className="inner-header">
              <LoggedinHeader amORGUser />
            </Row>
            <Row className="content-center pt-0">
              <Layout>
                <Layout>
                  <OrgSidebar amORGUser data={props} />
                  <Content>
                    <Form
                      className="input-md floating-label w-100"
                      name="validate_other"
                      initialValues={{
                        remember: true,
                      }}
                      form={form}
                    >
                      <Row className="py-3">
                        <h1 className="mb-0">
                          {"Duplicate Poll"}
                        </h1>
                      </Row>
                      <Spin spinning={loader} size="large">
                        <div className="mid-content">
                          <Row>
                            <Col span={24}>
                              {error && (
                                <Form.Item>
                                  <Alert
                                    message="Please fill the fields marked with asterisk(*)"
                                    type="error"
                                    showIcon
                                  ></Alert>
                                </Form.Item>
                              )}
                              <Col span={24}>
                                <Row>
                                  <Col span={24} md={18} className="gutter-row">
                                    <FloatLabel
                                      label="Poll Title"
                                      value={poll.pollName}
                                    >
                                      <TextArea
                                        showCount
                                        maxLength={50}
                                        htmlFor="polltitle"
                                        value={poll.pollName}
                                        id="pollName"
                                        onChange={(e) => onCreateChange(e)}
                                        onBlur={(e) => isPollNameDuplicate(e, "pollName")}
                                        autoComplete="off"
                                        className={`required ${styles.sm_textarea}`}
                                      />

                                      {pollError === "status: false" &&
                                        poll.pollName
                                          .replace(/^\s*/, "")
                                          .replace(/\s*$/, "").length > 0 &&
                                        poll.pollName.length > 0 && (
                                          <CheckCircleTwoTone
                                            className="successCheck"
                                            twoToneColor="#52c41a"
                                          />
                                        )}
                                      {pollError == "status: true" && (
                                        <Alert
                                          message="Sorry! This Poll title already exists in the system. Please use a different name."
                                          type="error"
                                          showIcon
                                        ></Alert>
                                      )}
                                    </FloatLabel>
                                  </Col>
                                  <Col span={24} md={18} className="gutter-row">
                                    <FloatLabel
                                      label="Description"
                                      value={poll.pollHelpText}
                                    >
                                      <Form.Item
                                        className="required"
                                        name="pollHelpText"
                                        id="pollHelpText"
                                        validateStatus="success"
                                      >
                                        <Input
                                          className="input-control"
                                          htmlFor="helptext"
                                          id="pollHelpText"
                                          name="pollHelpText"
                                          value={poll.pollHelpText}
                                          autoComplete="off"
                                          maxLength={500}
                                          onChange={(e) => onCreateChange(e)}
                                        />
                                      </Form.Item>
                                    </FloatLabel>
                                  </Col>
                                  <Col span={24} className="gutter-row mt-3">
                                      <h6>Upload</h6>
                                    <Form.Item
                                      name="upload"
                                      className="upload-list"
                                      valuePropName="fileList"
                                    >
                                       <UploadImage
                                        file={poll.pollDocument}
                                        handleStepOneUpload={handleStepOneUpload}
                                        handleBeforeUpload={handleBeforeUpload}
                                        handleOnRemove={handleOnRemove}
                                        handlePollImagePreview={handlePollImagePreview}
                                        handleCancel={handleCancel}
                                        poll={poll}
                                        />
                                      
                                    </Form.Item>
                                  </Col>
                                </Row>
                              </Col>
                              <Col span={24} className="gutter-row">
                                <Row className="align-items-center d-flex">
                                  <Col span={24} md={8} lg={8} xl={6}>
                                    <Form.Item
                                      name="showresult"
                                      label="Show result to users "
                                      className="mb-0"
                                    >
                                      <Switch
                                        id="showResult"
                                        checked={poll.showResult}
                                        onChange={(checked, e) =>
                                          onChange(checked, e)
                                        }
                                      />
                                    </Form.Item>
                                  </Col>
                                  {poll.showResult ? (
                                    <Col span={24} lg={6}>
                                      <FloatLabel
                                        label="Display final results as"
                                        value={poll.pollChartType}
                                      >
                                        <Form.Item
                                          className="required mb-0"
                                          name="result"
                                        >
                                          <>
                                            <Select
                                              className="input-control"
                                              htmlFor="result"
                                              className="required"
                                              value={
                                                poll.pollChartType === "2"
                                                  ? ""
                                                  : poll.pollChartType
                                              }
                                              onSelect={(value) =>
                                                handlePollOnSelect(
                                                  value,
                                                  "pollChartType"
                                                )
                                              }
                                            >
                                              <Select.Option value="1">
                                                Pie Chart
                                              </Select.Option>
                                              <Select.Option value="3">
                                                Bar Chart
                                              </Select.Option>
                                              <Select.Option value="4">
                                                Star Format
                                              </Select.Option>
                                            </Select>
                                          </>
                                        </Form.Item>
                                      </FloatLabel>
                                      {chartError && (
                                        <Form.Item>
                                          <Alert
                                            message="Please select the chat type as star"
                                            type="error"
                                            showIcon
                                          ></Alert>
                                        </Form.Item>
                                      )}
                                    </Col>
                                  ) : (
                                    <Col></Col>
                                  )}
                                </Row>
                              </Col>
                            </Col>
                          </Row>
                        </div>

                        <div className="mid-content mt-4">
                          <Row>
                            <Col span={24}>
                              <Col span={24}>
                                <Row className="d-block">
                                  <Col span={24} md={18} className="gutter-row">
                                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                                    <Col
                                      span={24}
                                      md={8}
                                      lg={8}
                                      className="mb-3 t-2 mb-y-0 gutter-row"
                                      >
                                    <FloatLabel
                                      label="Question Type"
                                      value={poll.pollQuestionType}
                                    >
                                      <Form.Item
                                        className="required"
                                        name="questiontype"
                                      >
                                        <>
                                          <Select
                                            className=""
                                            htmlFor="questiontype"
                                            className="required"
                                            id="pollQuestionType"
                                            value={poll.pollQuestionType}
                                            onSelect={(value) =>
                                              handlePollOnSelect(
                                                value,
                                                "pollQuestionType"
                                              )
                                            }
                                          >
                                            <Select.Option value="1">
                                              Single Choice
                                            </Select.Option>
                                            <Select.Option value="2">
                                              Multiple Choice
                                            </Select.Option>
                                            <Select.Option value="3">
                                              Star Rating
                                              {poll.status_id === 4}
                                            </Select.Option>
                                          </Select>
                                        </>
                                      </Form.Item>
                                    </FloatLabel>
                                  </Col>
                                  {poll.pollQuestionType === "3" ? (
                                   <Col
                                  span={24} md={10} lg={8}
                                  className="gutter-row"
                                  >
                                <FloatLabel
                                  label="Rating Type"
                                  value={poll.pollRatingType}
                                >
                                  <Form.Item
                                    className="required mb-0"
                                    name="ratingtype"
                                  >
                                    <>
                                      <Select
                                        className=""
                                        htmlFor="ratingtype"
                                        className="required"
                                        id="pollRatingType"
                                        value={poll.pollRatingType}
                                        onSelect={(value) =>
                                          handlePollOnSelect(
                                            value,
                                            "pollRatingType"
                                          )
                                        }
                                      >
                                        <Select.Option
                                          value="1"
                                        >
                                          Stars
                                        </Select.Option>
                                        <Select.Option
                                          value="2" 
                                        >
                                          Smiley 
                                        </Select.Option>
                                        <Select.Option
                                          value="3"
                                        >
                                          Thermometer
                                        </Select.Option>
                                        <Select.Option
                                          value="4"
                                          >
                                          ThumbsUp
                                        </Select.Option>
                                        <Select.Option
                                          value="5"
                                        >
                                          Ladder
                                        </Select.Option>
                                      </Select>
                                    </>
                                  </Form.Item>
                                </FloatLabel>
                                </Col>
                                     ): (null)}
                                   </Row>
                                  </Col>
                                  <Col span={24} md={18} className="gutter-row">

                                    <FloatLabel
                                      label="Question"
                                      value={poll.pollQuestion}
                                    >
                                      <Form.Item
                                        className="required edit-input"
                                        name="pollQuestion"
                                        id="pollQuestion"
                                      >
                                        <Input
                                          className="input-control edit-input"
                                          name="pollQuestion"
                                          id="pollQuestion"
                                          htmlFor="pollQuestion"
                                          className="required"
                                          defaultValue={poll.pollQuestion}
                                          value={poll.pollQuestion}
                                          onChange={(e) => onCreateChange(e)}
                                          autoComplete="off"
                                          maxLength={500}
                                        />
                                      </Form.Item>
                                    </FloatLabel>
                              
                              
                                  </Col>
                                  <Col span={24} className="mt-4">
                                    {poll.pollQuestionType === "3" && poll.pollRatingType ? (
                                      poll.pollRatingType === "1" ? (
                                        <ReactStars
                                        {...starsRating}
                                        value={poll.pollRatingResponse}
                                        name="pollRatingResponse"
                                        onChange={(value) =>
                                          handlePollOnSelect(
                                            value,
                                            "pollRatingResponse"
                                          )
                                        }
                                        classNames="mt-3"
                                      />
                                      ):
                                      poll.pollRatingType === "2" ? (<img src={Smiley} alt="send message" />):
                                      poll.pollRatingType === "3" ? (<img src={Thermometer} alt="send message" />):
                                      poll.pollRatingType === "4" ? (<img src={Thumb} alt="send message" />):
                                      poll.pollRatingType === "5" ? (<img src={Steps} alt="send message" />):
                                      (null)
                                      
                                    ) : poll.pollQuestionType === "1" ? (
                                      <div>
                                        <MultiChoice
                                          setSingleChoiceResponse={
                                            setSingleChoiceResponse
                                          }
                                          pollResponse={poll.pollResponse}
                                        />
                                      </div>
                                    ) : poll.pollQuestionType === "2" ? (
                                      <MultiChoice
                                        pollResponse={poll.pollResponse}
                                        setSingleChoiceResponse={
                                          setSingleChoiceResponse
                                        }
                                      />
                                    ) : null}
                                  </Col>
                                </Row>
                              </Col>

                              <div className="mt-5">
                                <Button
                                  type="primary"
                                  className="md mr-4"
                                  onClick={onPreview}
                                >
                                  Preview
                                </Button>
                                <Button
                                  type="secondary"
                                  type="secondary"
                                  htmlType="submit"
                                  onClick={onCancel}
                                >
                                  Cancel
                                </Button>
                              </div>
                            </Col>
                          </Row>
                        </div>
                      </Spin>
                    </Form>
                  </Content>
                </Layout>
              </Layout>
            </Row>
          </React.Fragment>
        ) : (
          <DuplicatePreviewPoll
            poll={poll}
            setPollsDate={setPollsDate}
            backtoEdit={backtoEdit}
            CreateDuplicatePoll={CreateDuplicatePoll}
            attachmentName={attachmentName}
            data={props}
          />
        )}
      </React.Fragment>
    </>
  );
};

export default DuplicatePoll;
