import React, { useState, useEffect } from "react";
import { Layout, Row, Col, Button, Breadcrumb } from "antd";
import { NavLink, useLocation } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import styles from "./managePoll.module.scss";
import OrgSidebar from "../../containers/OrgSidebar";
import ReactStars from "react-rating-stars-component";
import authHeader from "../../services/auth-header";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import { POLLS, POLL_RESULTS } from "../../headings";
import { POLL_RESULTS_NOT_AVAILABLE } from "../../messages";
import Thermometer from "../../assets/images/thermometer.svg";
import Thumb from "../../assets/images/Thumb.svg";
import Smiley from "../../assets/images/emoji.svg";
import Steps from "../../assets/images/steps.svg";

const starsRating = {
  size: 30,
  count: 5,
  isHalf: false,
  value: 3,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  onChange: (newValue) => {
    console.log(`Example 3: new value is ${newValue}`);
  },
};

const { Content } = Layout;

const PollResponse = (props) => {
  let location = useLocation();
  const [loader, setLoader] = useState(false);
  const [data, setData] = useState({});
  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    const data = {
      poll_id: location.state.poll_id,
    };
    axios.post(BASE_URL + "pollResult/", data, options).then((res) => {
      setData(res.data[0]);
      setLoader(false);
    });
  }, []);

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content>
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/pollsdirectory">
                      <Breadcrumb.Item>{POLLS}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{POLL_RESULTS}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3 justify-content-between">
                  <h1 className="mb-0">{location.state.title}</h1>
                  <Col
                    span={24}
                    md={24}
                    lg={12}
                    xl={15}
                    className="d-flex align-items-center justify-content-start justify-content-lg-end px-3 px-lg-0 my-4 my-lg-0"
                  >
                    {data?.total_employee_count ? (
                      <span className="pr-3">
                        Total audience : {data?.total_employee_count}
                      </span>
                    ) : null}
                    {/* <Button type="primary" className="md">
                      Download
                    </Button> */}
                  </Col>
                  <Col
                    span={24}
                    className="d-flex align-items-center justify-content-end"
                  ></Col>
                </Row>
                <div className="">
                  <Row>
                    <Col span={24}>
                      <p className="font-weight-md6">
                        {location.state.question}
                      </p>
                      {data?.total_response ? (
                        <span className="font-weight-md6 pt-3 d-flex">
                          {data?.total_response} Response(s)
                        </span>
                      ) : null}

                      <div className="mid-content d-flex justify-content-center align-items-center">
                        <Row>
                          <Col span={24}>
                            {/* <div className="c-vector-area">
                          <img
                           // src={CreateHotline}
                            alt="Create Hotline"
                            className="pr-4"
                          />
                        </div> */}
                            <h1 className="text-center pt-3">
                              {!data && POLL_RESULTS_NOT_AVAILABLE}
                            </h1>
                            <Row className="d-flex justify-content-center">
                              {/* <Divider className="mt-1" /> */}
                            </Row>
                            <Col
                              span={24}
                              className="d-flex align-items-center justify-content-center"
                            >
                              {data?.option_text === "1" ? (
                                <span>
                                  <ReactStars {...starsRating} />
                                </span>
                              ) : data?.option_text === "2" ? (
                                <img src={Smiley} alt="send message" />
                              ) : data?.option_text === "3" ? (
                                <img src={Thermometer} alt="send message" />
                              ) : data?.option_text === "4" ? (
                                <img src={Thumb} alt="send message" />
                              ) : data?.option_text === "5" ? (
                                <img src={Steps} alt="send message" />
                              ) : null}
                            </Col>
                          </Col>
                          <span className="pl-5">{data?.percentage}</span>
                        </Row>
                      </div>
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default PollResponse;
